@media @small-widths {
  .page-header__inner-wrapper {
    padding: 5px 0;
  }
  .page-header__nav {
    display: flex;
    align-items: center;
    flex-grow: 0;
  }
  .page-header--fixed .page-header__nav {
    flex-direction: row;
  }
}



