@media @small-widths {
  .store-filters-form {
    position: static;
    background: none;
  }
  .store-filters-lists {
    display: block;
    padding: 20px;
  }
  .store-filters-lists-platforms {
    width: 100%;
    margin-bottom: 25px;
    padding: 0;
    border-right: none;
  }
  .store-filters-lists-systems {
    width: 100%;
    margin-bottom: 25px;
    padding: 0;
    border-right: none;
  }
  .store-filters-lists-genres {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-bottom: 25px;
    padding: 0;
    border-right: none;
  }
  .store-filters-lists-genres-items {
    display: block;
  }
  .store-filters-lists-genres-items label {
    width: auto;
    padding-left: 0;
    padding-right: 0;
  }
  .store-filters-lists-genres-items label::before {
    left: -20px;
  }
  .store-filters-lists-types {
    width: 100%;
    padding: 0;
  }
  .store-filters-lists-title {
    margin-bottom: 10px;
    cursor: pointer;
  }
  .store-filters-lists-items {
    display: none;
  }
  .store-filters-lists label {
    margin-bottom: 10px;
    // text-align: center;
  }
  .store-filters-lists-btn-block {
    padding: 15px;
  }
}



// @media @extraSmall-widths {
