// }
@media @exxSmall-widths {
  .store-filters-lists-btn-block {
    display: block;
  }
  .store-filters-lists-btn-wrapper {
    padding-bottom: 15px;
  }
  .store-filters-lists-btn-wrapper:last-child {
    padding-bottom: 0;
  }
}
