.btn {
  box-sizing: border-box;
  display: inline-block;
  min-width: 165px;
  padding: 9px 15px;
  background-color: transparent;
  border: 2px solid #dc291e;
  border-radius: 0;
  font-size: 10px;
  line-height: 1.3;
  font-weight: normal;
  color: #f4f3f3;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  transition: background-color 0.5s, border 0.5s, color 0.5s;
}
.btn:hover {
  background-color: #dc291e;
  color: #f4f3f3;
  text-decoration: none;
}
.btn:focus {
  color: #f4f3f3;
  outline: none;
  text-decoration: none;
}
.corner-box {
  position: relative;
}
.corner-box::before,
.corner-box::after {
  content: "";
  position: absolute;
  top: 0;
  width: calc((100% - 17px) / 2);
  height: 2px;
  background-color: #dc291e;
}
.corner-box::before {
  left: 0;
}
.corner-box::after {
  right: 0;
}
.corner {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  width: 35px;
  height: 11px;
  background-image: url("../img/corner-down.png");
  background-repeat: no-repeat;
  background-position: center;
}
.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  border-radius: 0;
}
.pagination-items {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 15px;
}
.pagination-items li {
  padding: 10px 15px;
}
.pagination-items li a {
  font-size: 12px;
  font-weight: normal;
  color: #f4f3f3;
  transition: color .5s;
}
.pagination-items li a:hover {
  color: #ed1c24;
  text-decoration: none;
}
.pagination-btn-prev {
  position: relative;
  flex-shrink: 0;
  width: 31px;
  height: 30px;
  font-size: 0;
  cursor: pointer;
  z-index: 100;
}
.pagination-btn-prev::before,
.pagination-btn-prev::after {
  content: "";
  position: absolute;
  left: 10px;
  top: 14px;
  width: 5px;
  height: 1px;
  transform-origin: .5px 50%;
  background-color: #ffffff;
  transition: background-color 0.5s, box-shadow 0.5s;
}
.pagination-btn-prev::after {
  transform: rotate(50deg);
  box-shadow: 4px -4.5px 0 0 #ffffff;
}
.pagination-btn-prev::before {
  transform: rotate(-50deg);
  box-shadow: 4px 4.5px 0 0 #ffffff;
}
.pagination-btn-prev:hover::before,
.pagination-btn-prev:hover::after {
  background-color: #ed1c24;
}
.pagination-btn-prev:hover::after {
  box-shadow: 4px -4.5px 0 0 #ed1c24;
}
.pagination-btn-prev:hover::before {
  box-shadow: 4px 4.5px 0 0 #ed1c24;
}
.pagination-btn-next {
  position: relative;
  flex-shrink: 0;
  width: 31px;
  height: 30px;
  font-size: 0;
  cursor: pointer;
  z-index: 100;
}
.pagination-btn-next::before,
.pagination-btn-next::after {
  content: "";
  position: absolute;
  right: 10px;
  top: 14px;
  width: 5px;
  height: 1px;
  transform-origin: 4.5px 50%;
  background-color: #ffffff;
  transition: background-color 0.5s, box-shadow 0.5s;
}
.pagination-btn-next::before {
  transform: rotate(50deg);
  box-shadow: -4px 4.5px 0 0 #ffffff;
}
.pagination-btn-next::after {
  transform: rotate(-50deg);
  box-shadow: -4px -4.5px 0 0 #ffffff;
}
.pagination-btn-next:hover::before,
.pagination-btn-next:hover::after {
  background-color: #ed1c24;
}
.pagination-btn-next:hover::before {
  box-shadow: -4px 4.5px 0 0 #ed1c24;
}
.pagination-btn-next:hover::after {
  box-shadow: -4px -4.5px 0 0 #ed1c24;
}
@media (max-width: 767px) {
  .pagination-items li {
    padding: 10px;
  }
}
@media (max-width: 479px) {
  .pagination-items {
    margin: 0 5px;
  }
  .pagination-items li {
    padding: 10px 8px;
  }
}
.items-settings-wrapper {
  padding: 0 15px;
}
.items-settings {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  max-width: 1640px;
  margin: 0 auto;
  padding: 20px;
}
.items-settings-btn-wrapper {
  box-sizing: border-box;
  display: flex;
  width: 30%;
}
.items-settings-btn {
  box-sizing: border-box;
  display: inline-block;
  min-width: 165px;
  padding: 9px 15px;
  background-color: transparent;
  border: 2px solid #dc291e;
  border-radius: 0;
  font-size: 10px;
  line-height: 1.3;
  font-weight: normal;
  color: #f4f3f3;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  transition: background-color 0.5s, border 0.5s, color 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: 34px;
  border: 2px solid #f4f3f3;
}
.items-settings-btn:hover {
  background-color: #dc291e;
  color: #f4f3f3;
  text-decoration: none;
}
.items-settings-btn:focus {
  color: #f4f3f3;
  outline: none;
  text-decoration: none;
}
.items-settings-btn:hover {
  background-color: #dc291e;
  color: #f4f3f3;
  text-decoration: none;
}
.items-settings-btn:focus {
  color: #f4f3f3;
  outline: none;
  text-decoration: none;
}
.items-settings-btn:hover {
  background-color: #dc291e;
  border: 2px solid #dc291e;
}
.items-settings-btn span {
  margin-right: 12px;
}
.items-settings-btn span img {
  display: block;
  width: 100%;
}
.items-settings-per-page {
  box-sizing: border-box;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30%;
  margin: 0 auto;
  padding: 0 20px;
  font-size: 16px;
  font-weight: normal;
  color: #f4f3f3;
  text-transform: uppercase;
}
.items-settings-per-page-current {
  position: relative;
  padding-right: 30px;
  text-align: center;
  cursor: pointer;
}
.items-settings-per-page-current::before,
.items-settings-per-page-current::after {
  content: "";
  position: absolute;
  right: 7px;
  top: 9px;
  width: 2px;
  height: 5px;
  transform-origin: 50% .5px;
  background-color: #ed1c24;
  transition: top .5s, transform .5s;
}
.items-settings-per-page-current::before {
  transform: rotate(135deg);
}
.items-settings-per-page-current::after {
  transform: rotate(-135deg);
}
.items-settings-per-page--opened .items-settings-per-page-current::before,
.items-settings-per-page--opened .items-settings-per-page-current::after {
  top: 6px;
}
.items-settings-per-page--opened .items-settings-per-page-current::before {
  transform: rotate(45deg);
}
.items-settings-per-page--opened .items-settings-per-page-current::after {
  transform: rotate(-45deg);
}
.items-settings-per-page-items-wrapper {
  position: absolute;
  top: calc(100% + 7px);
  left: 50%;
  transform: translate(-50%, 0);
  height: 0;
  padding: 0;
  overflow: hidden;
}
.items-settings-per-page--opened .items-settings-per-page-items-wrapper {
  height: auto;
  padding: 1px;
}
.items-settings-per-page-items {
  box-sizing: border-box;
  width: 100%;
  transform: translate(0, -100%);
  padding: 5px 0;
  background-color: rgba(16, 15, 15, 0.88);
  border: 1px solid #dc291e;
  transition: transform .5s;
}
.items-settings-per-page--opened .items-settings-per-page-items {
  transform: translate(0, 0);
}
.items-settings-per-page-item {
  padding: 5px 10px;
  font-size: 14px;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
}
.items-settings-per-page-item:hover {
  background-color: rgba(42, 40, 40, 0.88);
}
.items-settings-per-page-item--active {
  background-color: rgba(42, 40, 40, 0.88);
}
.items-settings-info {
  box-sizing: border-box;
  width: 30%;
  font-size: 10px;
  font-weight: normal;
  color: #f4f3f3;
  text-transform: uppercase;
  text-align: right;
}
@media (max-width: 991px) {
  .items-settings {
    flex-wrap: wrap;
  }
  .items-settings-btn-wrapper {
    width: auto;
    margin-right: auto;
  }
  .items-settings-per-page {
    width: 100%;
    margin-top: 15px;
    font-size: 14px;
    order: 1;
  }
  .items-settings-per-page-current {
    width: 200px;
  }
  .items-settings-info {
    width: auto;
  }
}
@media (max-width: 479px) {
  .items-settings {
    flex-wrap: nowrap;
    flex-direction: column;
  }
  .items-settings-btn-wrapper {
    width: auto;
    margin-right: 0;
    margin-bottom: 15px;
  }
}
