  display: flex;
  padding-left: 30px;
}
.settings-filters-blocks-toggle {
  display: none;
}
.settings-filters-blocks {
  display: flex;
  width: 100%;
}
.settings-filters-block {
  position: relative;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  width: 25%;
  padding: 0 25px;
}
.settings-filters-block-current {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 14px 0;
  font-size: 16px;
  font-weight: normal;
  color: #f4f3f3;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 2px 2px 0 #000000;
  cursor: pointer;
}
.settings-filters-block-icon {
  position: relative;
  align-self: center;
  width: 20px;
  height: 14px;
}
.settings-filters-block-icon::before,
.settings-filters-block-icon::after {
  content: "";
  position: absolute;
  right: 3px;
  top: 8px;
  width: 2px;
  height: 5px;
  transform-origin: 50% .5px;
  background-color: #100f0f;
  transition: top .5s, transform .5s;
}
.settings-filters-block-icon::before {
  transform: rotate(135deg);
}
.settings-filters-block-icon::after {
  transform: rotate(-135deg);
}
.settings-filters-block--opened .settings-filters-block-icon::before,
.settings-filters-block--opened .settings-filters-block-icon::after {
  top: 5px;
}
.settings-filters-block--opened .settings-filters-block-icon::before {
  transform: rotate(45deg);
}
.settings-filters-block--opened .settings-filters-block-icon::after {
  transform: rotate(-45deg);
}
.settings-filters-block-items-wrapper {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, 0);
  height: 0;
  overflow: hidden;
  z-index: 100;
  transition: height .5s;
}
.settings-filters-block-items {
  background-color: #100f0f;
  border: 1px solid #3e3e3e;
  border-radius: 2px;
