.fanart-promo-wrapper {
  padding-top: 20px;
  padding-bottom: 50px;
  background-image: url("../img/fanart/fanart-img-bg.png");
  background-position: center;
  background-size: cover;
  background-color: #100f0f;
}
.fanart-promo {

}
.fanart-promo-title {
  margin-bottom: 15px;
}
.fanart-promo-descr {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: bold;
  color: @color-text;
  text-align: center;
  text-transform: uppercase;
}



@media @medium-widths {
  .fanart-promo-descr {
    font-size: 15px;
  }
}



@media @extraSmall-widths {
  .fanart-promo-descr {
    font-size: 14px;
  }
}
