@media @xlarge-widths {
  .nav-tabs.product-nav-tabs li a {
    font-size: 16px;
  }
  .product-nav-tabs-social {
    width: 380px;
    margin-left: 40px;
  }
}



@media @large-widths {
  .nav-tabs.product-nav-tabs li a {
    font-size: 15px;
  }
  .product-nav-tabs-social {
    width: 350px;
    margin-left: 30px;
  }
}



@media @medium-widths {
  .nav-tabs.product-nav-tabs li a {
    padding: 10px 7px;
    font-size: 12px;
  }
  .product-nav-tabs-social {
    width: 300px;
    margin-left: 20px;
  }
}



@media @small-widths {
  .product-nav-tabs-wrap {
    flex-direction: column-reverse;
  }
  .product-nav-tabs-social {
    width: auto;
    justify-content: flex-start;
    margin-left: 0;
    margin-bottom: 10px;
  }
  .product-block {
    display: block;
  }
}



@media @extraSmall-widths {
  .product-nav-tabs li {
    width: auto;
  }
}



