@media @xlarge-widths {
  .product-sidebar {
    width: 380px;
    margin-left: 40px;
  }
}



@media @large-widths {
  .product-sidebar {
    width: 350px;
    margin-left: 30px;
  }
}



@media @medium-widths {
  .product-sidebar {
    width: 300px;
    margin-left: 20px;
  }
  .product-sidebar-categiries-item {
    padding: 7px 15px;
  }
}



@media @small-widths {
  .product-sidebar {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-left: 0;
    padding: 25px;
  }
  .product-sidebar-categiries {
    width: 50%;
    padding-left: 20px;
  }
  .product-sidebar-redeem {
    flex-shrink: 0;
    width: 350px;
    margin-left: auto;
  }
  .product-sidebar-recommnded {
    width: 100%;
  }
  .product-sidebar-recommnded-items {
    display: flex;
    flex-wrap: wrap;
  }
  .product-sidebar-recommnded-item {
    box-sizing: border-box;
    width: 320px;
    padding: 0 10px;
    margin-left: auto;
    margin-right: auto;
  }
}



@media (max-width: 840px) {
  .product-sidebar-categiries {
    width: 100%;
  }
  .product-sidebar-redeem {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
  }
}



@media @extraSmall-widths {
  .product-sidebar-recommnded-item {
    box-sizing: border-box;
    width: 280px;
    margin-left: auto;
    margin-right: auto;
  }
}



@media @exxSmall-widths {
  .product-sidebar {
    padding-left: 0;
    padding-right: 0;
  }
  .product-sidebar-categiries-item {
    padding: 5px 12px;
    font-size: 11px;
  }
  .product-sidebar-redeem {
    width: 100%;
    max-width: 350px;
    margin-left: auto;
  }
}
